import React from 'react';
import imgAttack from 'assets/img-attack.jpg';
import { Webinar } from 'common/styles';

import * as S from './styles';

function Attack() {
  return (
    <S.Wrapper>
      <section>
        <figure>
          <img src={imgAttack} alt="" />
        </figure>
        <article>
          <h2>Why wait for an attack?</h2>
          <p>
            There is no need to risk a cyber-attack when ALSO offers the most
            sustainable, cost-effective and business-friendly gateway solution
            available - NoSpamProxy®.
          </p>
          <p>
            To keep your business fully NoSpamProxy® protected from damaging
            cyber-attacks, contact your ALSO sales team.{' '}
          </p>
          <a href="/" className="get-protected">
            <span className="cta-label">Get Protected</span>
            <span className="cta-arrow"></span>
          </a>

          <Webinar>
            <header>
              <h3>
                Take part in our exclusive ALSO Partner Webinar on 10:00 -
                11:00am (EEST Time), 20.11.2019 and learn more about the
                NoSpamProxy® features that keep your business cyber-secure.
              </h3>
            </header>
            <a href="#webinar-form">
              <span className="cta-label">Get Webinar</span>
              <span className="cta-arrow"></span>
            </a>
          </Webinar>
        </article>
      </section>
    </S.Wrapper>
  );
}

export default Attack;
