import React from 'react';
import imgSafe from 'assets/img-safe.jpg';
import { Quote } from 'common/styles';

import * as S from './styles';

function Safe() {
  return (
    <S.Wrapper>
      <section>
        <article>
          <h2>Keeping your communications safe</h2>
          <p>
            With NoSpamProxy® Encryption, you can encrypt sensitive data and
            send it in a form compliant with EU General Data Protection
            Regulation (GDPR). The tool also includes interfaces with leading
            trust centers.
          </p>
          <p>
            NoSpamProxy was developed in Germany, the country with the highest
            privacy and security standards in the EU, and is the recipient of
            TeleTrust’s “Security made in Germany” Safe.
          </p>
          <Quote>
            <small>
              “NoSpamProxy® Encryption runs absolutely stable and makes our
              daily work much easier.”{' '}
              <span>- Georg Ries, IT Manager at isarpatent</span>
            </small>
          </Quote>
        </article>
        <figure>
          <img src={imgSafe} alt="" />
        </figure>
      </section>
    </S.Wrapper>
  );
}

export default Safe;
