import styled from 'styled-components';

export const Wrapper = styled.div`
  width: 100%;
  padding: 60px 40px;
  background: rgb(55, 0, 114);
  background: linear-gradient(
    180deg,
    rgba(55, 0, 114, 1) 0%,
    rgba(123, 42, 112, 1) 51%,
    rgba(209, 95, 97, 1) 100%
  );
  color: #fff;
  @media (max-width: 1000px) {
    padding: 40px 25px;
  }

  > section {
    max-width: 1080px;
    width: 100%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 1000px) {
      flex-direction: column;
    }
  }
`;

export const Details = styled.div`
  flex: 0 0 50%;
  max-width: 460px;
  @media (max-width: 1000px) {
    max-width: 600px;
    margin: auto;
  }

  h1 {
    font-size: 60px;
    @media (max-width: 600px) {
      font-size: 40px;
    }
  }
  h2 {
    font-size: 40px;
    margin: 30px 0;
    @media (max-width: 600px) {
      font-size: 28px;
    }
  }
  p {
    line-height: 1.4;
    margin: 20px 0;
    font-size: 16px;
  }
`;

export const Forms = styled.div`
  flex: 0 0 50%;
  @media (max-width: 1000px) {
    margin-top: 40px;
  }

  .img-mail {
    max-width: 440px;
    width: 100%;
    margin: auto;
    display: block;
  }
  .webinar-form {
    max-width: 340px;
    width: 100%;
    margin: 40px auto 0;
    display: flex;
    flex-direction: column;
    @media (max-width: 1000px) {
      max-width: 600px;
    }

    > aside {
      display: flex;
      align-items: center;
      label {
        font-size: 12px;
        margin: 0 0 0 10px;
      }
    }
    p {
      font-size: 12px;
      a {
        text-decoration: underline;
        color: #fff;
      }
    }
    button {
      height: 40px;
      width: 100%;
      font-size: 16px;
      text-transform: uppercase;
      color: #fff;
      border: none;
      background: #62eb5f;
      font-weight: 700;
      margin: 20px 0;
    }
  }
  .textfield {
    height: 40px;
    margin-bottom: 20px;
    border: none;
    padding: 0 15px;
    background: rgba(255, 255, 255, 0.7);
  }
`;
