import React from 'react';
import imgSetup from 'assets/img-setup.jpg';

import * as S from './styles';

function Setup() {
  return (
    <S.Wrapper>
      <section>
        <article>
          <h2>Easy to set up and integrate</h2>
          <p>
            NoSpamProxy® software components are seamlessly integrated into
            Office 365 environments. Deployment can be on Azure or a managed
            server platform of your choice. Hybrid scenarios including cloud and
            classic on premises Microsoft Server and Microsoft Exchange can also
            be easily set up. To operate the Web portal for transferring large
            files, and for encrypted communication with email recipients who do
            not possess their own certificate or key, the Internet Information
            Server (IIS), which is available in all Microsoft environments,
            ensures that no extra software components are needed.
          </p>
        </article>
        <figure>
          <img src={imgSetup} alt="" />
        </figure>
      </section>
    </S.Wrapper>
  );
}

export default Setup;
