import React from 'react';
import imgCloud from 'assets/img-cloud.png';
import { Webinar, Quote } from 'common/styles';

import * as S from './styles';

function Secure() {
  return (
    <S.Wrapper>
      <section>
        <figure>
          <img src={imgCloud} alt="" />
        </figure>
        <article>
          <h2>Securely send and receive large files </h2>
          <p>
            NoSpamProxy® Large Files allows you to send file attachments of any
            size with a high-level of protection via TLS (Transport Layer
            Security) encryption. This allows sensitive files to be easily,
            quickly and securely shared and distributed from within your
            business’s email service.
          </p>
          <Quote>
            <small>
              “In contrast to a conventional FTP server, NoSpamProxy® Large
              Files integrates all data handling into the familiar email
              environment of our employees, facilitating a high level of
              security.”{' '}
              <span>
                - Peter Gerfer, IT Systems Engineer, Deutscher Ärzte-Verlag
              </span>
            </small>
          </Quote>

          <Webinar>
            <header>
              <h3>
                Take part in our exclusive ALSO Partner Webinar on 10:00 -
                11:00am (EEST Time), 20.11.2019 and learn more about the
                NoSpamProxy® features that keep your business cyber-secure.
              </h3>
            </header>
            <a href="#webinar-form">
              <span className="cta-label">Get Webinar</span>
              <span className="cta-arrow"></span>
            </a>
          </Webinar>
        </article>
      </section>
    </S.Wrapper>
  );
}

export default Secure;
