import React from 'react';
import imgAward from 'assets/img-award.jpg';
import { Quote } from 'common/styles';

import * as S from './styles';

function Award() {
  return (
    <S.Wrapper>
      <section>
        <article>
          <h2>Award-winning protection</h2>
          <p>
            What three things offer peace of mind with regards to business email
            communication? Protection, Protection, Protection. NoSpamProxy®
            offers a robust defense against spam and ransomware, a steadfast
            encryption guard and a speedy, secure delivery tool for large files.
            It has been awarded the Virus Bulletin Spam + Gold rating for IT
            Security with a score of 99.99 out of 100 and has a 0.00%
            false-positive rate. Basically, it’s a leading gateway solution.
          </p>
          <Quote>
            <small>
              “Not only does NoSpamProxy® perform brilliantly in terms of spam
              protection, but it also offers cutting-edge protection from
              malware and ransomware”.{' '}
              <span>- Martijn Grooten, Editor-in-Chief, Virus Bulletin</span>
            </small>
          </Quote>
        </article>
        <figure>
          <img src={imgAward} alt="" />
        </figure>
      </section>
    </S.Wrapper>
  );
}

export default Award;
