import React from 'react';
import logo from 'assets/logo-also.png';

import * as S from './styles';

function Navigation() {
  return (
    <S.Wrapper>
      <section>
        <img src={logo} alt="" className="logo"/>
        <nav>
          <a href="/">Contact</a> | <a href="/">Login</a>
        </nav>
      </section>
    </S.Wrapper>
  );
}

export default Navigation;
