import styled from 'styled-components';
import iconArrow from 'assets/icon-arrow-circle.svg';

export const Webinar = styled.div`
  margin-top: 40px;
  width: 100%;

  header {
    background: #000;
    padding: 25px;
  }
  h3 {
    color: #fff;
    font-size: 20px;
    font-weight: 400;
    @media (max-width: 600px) {
      font-size: 18px;
    }
  }
  a {
    padding: 10px 30px;
    display: flex;
    color: #fff;
    align-items: center;
    background: #62eb5f;
    justify-content: space-between;
    cursor: pointer;
  }
  .cta-label {
    font-weight: 700;
    font-size: 28px;
    display: block;
    flex: 0 0 200px;
    @media (max-width: 600px) {
      font-size: 22px;
      flex: 0 0 150px;
    }
  }
  .cta-arrow {
    flex: 1;
    width: 100%;
    position: relative;
    height: 38px;
    display: block;

    &:before {
      width: calc(100% - 36px);
      height: 2px;
      background: #fff;
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(0, -50%);
    }
    &:after {
      width: 38px;
      height: 38px;
      content: '';
      background: url(${iconArrow}) center/contain no-repeat;
      position: absolute;
      right: 0;
      top: 0;
    }
  }
`;

export const Quote = styled.div`
  background: #000;
  padding: 15px;
  margin-top: 30px;

  small {
    font-size: 14px;
    line-height: 1.4;
    color: #fff;

    span {
      color: #c3e17e;
    }
  }
`;
