import styled from 'styled-components';

export const Wrapper = styled.div`
  width: 100%;
  background: #fff;
  padding: 0 25px;

  > section {
    max-width: 1200px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .logo {
    padding: 10px 0 20px;
    display: block;
  }
  nav {
    color: #ccc;
    a {
      font-size: 16px;
      color: #999;
    }
    a:hover {
      text-decoration: underline;
    }
  }
`;
