import styled from 'styled-components';

export const Wrapper = styled.div`
  width: 100%;
  padding: 80px 40px;
  color: #fff;
  background: rgb(27, 55, 123);
  background: linear-gradient(
    180deg,
    rgba(27, 55, 123, 1) 0%,
    rgba(44, 113, 141, 1) 51%,
    rgba(67, 186, 163, 1) 100%
  );
  @media (max-width: 1000px) {
    padding: 40px 25px;
  }
  > section {
    max-width: 1080px;
    width: 100%;
    margin: auto;
    display: flex;
    align-items: center;
    @media (max-width: 1000px) {
      flex-direction: column;
    }

    > article {
      flex: 0 0 50%;
      max-width: 470px;
      @media (max-width: 1000px) {
        max-width: 600px;
      }
      h2 {
        font-size: 32px;
        @media (max-width: 600px) {
          font-size: 26px;
        }
      }
      p {
        font-size: 16px;
        margin: 20px 0;
        line-height: 1.4;
      }
    }
    > figure {
      flex: 0 0 50%;
      @media (max-width: 1000px) {
        margin-bottom: 40px;
      }

      img {
        width: 100%;
        display: block;
        max-width: 435px;
      }
    }
  }
`;
