import React from 'react';
import imgInbox from 'assets/img-inbox.jpg';
import { Quote } from 'common/styles';

import * as S from './styles';

function Inbox() {
  return (
    <S.Wrapper>
      <section>
        <figure>
          <img src={imgInbox} alt="" />
        </figure>
        <article>
          <h2>Defending your inbox</h2>
          <p>
            NoSpamProxy® Protection works by catching emails that are a risk to
            your business. The tool works by examining all incoming emails using
            a variety of rules and filters. Upon discovering a potential threat,
            NoSpamProxy rejects the incoming mail and alerts the sender with a
            non-delivery message. NoSpamProxy also works without spam queues,
            substantially reducing the need for administration.
          </p>
          <p>
            NoSpamProxy® Level of Trust technology automatically builds a secure
            list of approved senders.
          </p>
          <Quote>
            <small>
              “The Level of Trust filter is an excellent idea. It reliably stops
              known spammers and guarantees that emails from our regular
              correspondents are delivered.”{' '}
              <span>- Jürgen Lalla, IT-Manager Swiss Life Select</span>
            </small>
          </Quote>
        </article>
      </section>
    </S.Wrapper>
  );
}

export default Inbox;
