import styled from 'styled-components';

export const Wrapper = styled.div`
  background: #f2f2f2;
  width: 100%;
  padding: 80px 40px 0;
  @media (max-width: 1000px) {
    padding: 40px 25px 0;
  }
  > section {
    max-width: 1080px;
    width: 100%;
    margin: auto;
    display: flex;
    align-items: center;
    @media (max-width: 1000px) {
      flex-direction: column;
    }

    > article {
      flex: 0 0 50%;
      max-width: 470px;
      @media (max-width: 1000px) {
        max-width: 600px;
        order: 0;
      }
      h2 {
        font-size: 32px;
        @media (max-width: 600px) {
          font-size: 26px;
        }
      }
      p {
        font-size: 16px;
        margin: 20px 0;
        line-height: 1.4;
      }
    }
    > figure {
      flex: 0 0 50%;
      @media (max-width: 1000px) {
        order: 1;
        margin-top: 40px;
      }

      img {
        width: 100%;
        display: block;
        max-width: 435px;
      }
    }
  }
`;
