import React from 'react';

import imgHeader from 'assets/img-header.png';
import { Webinar } from 'common/styles';

import * as S from './styles';

function Header() {
  return (
    <S.Wrapper>
      <section>
        <S.Details>
          <h1>NoSpamProxy</h1>
          <h2>Safeguard your business from email cyber attacks</h2>
          <p>
            Falling victim to a cyber-attack is a case of when, not if. With 82%
            of all attacks launched via email, why leave your business
            vulnerable to attack when NoSpamProxy® is available via ALSO?
          </p>
          <p>
            NoSpamProxy® is an email gateway solution that is simple to use,
            secure and effective. It makes sure your work processes are never
            compromised by those with malicious and criminal intentions. Its
            automated functions keep your communications as they should be – for
            your eyes only.
          </p>
          <Webinar>
            <header>
              <h3>
                Sign up to our exclusive <strong>ALSO Partner Webinar</strong>{' '}
                on why your business is more cyber-secure with NoSpamProxy® at
                10:00 - 11:00am (EEST Time) on 20.11.2019.
              </h3>
            </header>
            <a href="#webinar-form">
              <span className="cta-label">Get Webinar</span>
              <span className="cta-arrow"></span>
            </a>
          </Webinar>
        </S.Details>
        <S.Forms>
          <img src={imgHeader} alt="" className="img-mail" id="webinar-form" />
          <div className="webinar-form">
            <input type="text" placeholder="Name" className="textfield" />
            <input type="email" placeholder="Email" className="textfield" />
            <input
              type="text"
              placeholder="Phone Number (optional)"
              className="textfield"
            />
            <input type="text" placeholder="Country" className="textfield" />
            <aside>
              <input
                type="checkbox"
                id="vehicle1"
                name="vehicle1"
                value="Bike"
              />
              <label htmlFor="vehicle1">
                Yes, I agree that an ALSO employee can contact me in relation to
                the topic either via phone or email.*
              </label>
            </aside>
            <button>get started</button>
            <p>
              By signing up you accept our <a href="/">terms of use</a> and{' '}
              <a href="/">data privacy statement</a>
            </p>
          </div>
        </S.Forms>
      </section>
    </S.Wrapper>
  );
}

export default Header;
