import styled from 'styled-components';
import iconArrow from 'assets/icon-arrow-circle.svg';

export const Wrapper = styled.div`
  width: 100%;
  padding: 80px 40px;
  background: #f0f0f0;
  @media (max-width: 1000px) {
    padding: 40px 25px;
  }
  > section {
    max-width: 1080px;
    width: 100%;
    margin: auto;
    display: flex;
    align-items: center;
    @media (max-width: 1000px) {
      flex-direction: column;
    }

    > article {
      flex: 0 0 50%;
      max-width: 470px;
      @media (max-width: 1000px) {
        max-width: 600px;
      }
      h2 {
        font-size: 32px;
        @media (max-width: 600px) {
          font-size: 26px;
        }
      }
      p {
        font-size: 16px;
        margin: 20px 0;
        line-height: 1.4;
      }
    }
    > figure {
      flex: 0 0 50%;
      @media (max-width: 1000px) {
        margin-bottom: 40px;
      }

      img {
        width: 100%;
        display: block;
        max-width: 360px;
      }
    }
  }

  .get-protected {
    padding: 10px 30px;
    margin: 40px 0 60px;
    display: flex;
    color: #fff;
    align-items: center;
    background: #000;
    justify-content: space-between;
    cursor: pointer;
  }
  .cta-label {
    font-weight: 700;
    font-size: 28px;
    display: block;
    flex: 0 0 200px;
    @media (max-width: 600px) {
      font-size: 22px;
      flex: 0 0 150px;
    }
  }
  .cta-arrow {
    flex: 1;
    width: 100%;
    position: relative;
    height: 38px;
    display: block;

    &:before {
      width: calc(100% - 36px);
      height: 2px;
      background: #fff;
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(0, -50%);
    }
    &:after {
      width: 38px;
      height: 38px;
      content: '';
      background: url(${iconArrow}) center/contain no-repeat;
      position: absolute;
      right: 0;
      top: 0;
    }
  }
`;
